.member-nav .active::after{
  position: absolute;
  content: '';
  width: 100%;
  background: #916000;
  height: 2px;
  left: 0;
  bottom: -4px;
}

.custom-control-input:checked~.custom-control-label::before{
  color: #fff!important;
  border-color: #916000!important;
  background-color: #916000!important;
}